.reviews{
  padding: 2.5em 0;
}

.reviews-slider{
  margin: 2.5em auto 0;
  width: 85.75em;
  max-width: 100%;
  position: relative;
  .swiper{
    &-container{
      width: 50em;
      margin: 0 auto;
      max-width: 100%;
      position: relative;
      &:after,
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 7.875em;
        height: calc(100% - 6.25em);
        border-radius: .625em;
        background: #f1f8ff;
        opacity: 0.5;
        z-index: -1;
      }
      &:before{
        left: -1.875em;
      }
      &:after{
        right: -1.875em;
      }
    }
    &-button{
      border: 1px solid $light-blue;
      border-radius: 50%;
      width: 3.75em;
      height: 3.75em;
      @include center;
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
      svg{
        width: 1.5em;
      }
      &:hover{
        background-color: $light-blue;
        path{
          //fill: #fff;
        }
      }
    }
  }
}

.review-card{
  padding: 2.5em;
  text-align: center;
  background: #f1f8ff;
  border-radius: .625em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img{
    margin-top: 2.5em;
    border-radius: .625em;
    width: 3.125em;
    height: 3.125em;
    object-fit: cover;
  }
  &__info{
    margin-top: .625em;
    @include grid100gap(.25em);
  }
  &__site{
    margin-top: .625em;
    height: 5em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.partners{
  padding: 5em 0;
  &__grid{
    width: 62.5em;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 50% 50%;
    position: relative;
  }
  &__by{
    @include centerAbsolute;
    width: 25em;
    height: 25em;
    border-radius: 50%;
    background-color: $primary;
    color: #fff;
    @include center;
  }
}

.partners-item{
  border: 3px solid $light-blue;
  border-radius: 50%;
  &__inner{
    padding-bottom: 100%;
    position: relative;
  }
  img{
    width: 10.625em;
    @include centerAbsolute;
  }
}

.main__link,
.contacts__link,
.marketing__link{
  @include center;
  flex-direction: column;
  background-color: $primary;
  color: #fff;
  border-radius: 50%;
  &:hover{
    background-color: darken($primary,10%);
    color: #fff;
  }
  img{
    width: 3.125em;
    margin: 0 0 .625em;
  }
}

.contacts{
  background-color: #061D61;
  //background-image: url(../img/contacts-bg.svg);
  //background-size: cover;
  //background-position: center;
  border-radius: 5em;
  position: relative;
  text-align: center;
  padding: 8.125em 0 15.3125em;
  margin: 0 auto 5em;
  width: calc(100% - 2.5em);
  color: #fff;
  &__desc{
    margin: 1.5em auto 0;
    width: 42.25em;
    max-width: 100%;
  }
  &__link{
    position: absolute;
    bottom: -6em;
    left: 50%;
    transform: translateX(-50%);
    width: 18.75em;
    height: 18.75em;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 1.25em 0;
    overflow: hidden;
    .h1{
      width: 90%;
      margin: 0 auto;
    }
  }

  .reviews-slider{
    margin-top: 1.75em;
    .swiper{
      &-button{
        width: 2em;
        height: 2em;
        &-prev{
          left: -1em;
        }
        &-next{
          right: -1em;
        }
        svg{
          width: 1em;
        }
      }
    }
  }

  .review-card{
    padding: 1.25em;
  }

  .partners{
    padding: 2.5em 0;
    &__grid{
      font-size: .75em;
    }
  }

  .partners-item{
    img{
      width: 20em;
    }
  }

  .contacts__link,
  .marketing__link{
    position: static;
    padding: 0 3em;
    width: auto;
    height: 3.125em;
    flex-direction: row;
    border-radius: 2em;
    img{
      width: 2.5em;
      margin: 0 .5em 0 0;
    }
  }

  .contacts{
    margin-bottom: 2.5em;
    font-size: .75em;
    border-radius: 2.5em;
    padding: 2.5em 0;
    .container{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__link{
      margin-top: 2em;
      transform: unset;
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .review-card{
    font-size: .875em;
  }

  .partners{
    &__grid{
      font-size: 1em;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__by{
      order: -1;
      width: auto;
      height: auto;
      background-color: transparent;
      color: $black;
      transform: unset;
      position: static;
    }
  }

  .partners-item{
    img{
      width: 12em;
    }
  }

  .contacts{
    border-radius: 1.25em;
    width: 100%;
    font-size: 1em;
    &__link{
      text-align: left;
      padding: 0 1.5em;
      span{
        font-size: 1em;
      }
    }
  }
}
