@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.75em;
    height: 1.75em;
    @include lastM(0 1.25em 0 0);
    @include center;
    &:hover{
      opacity: .8;
    }
    svg{
      width: 100%;
    }
  }
}
