@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.125em;
    height: 3.125em;
    border-radius: .625em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}
