.service{
  padding: 5em 0;
  &__grid{
    margin-top: 2.5em;
  }
}

.service-card{
  padding: 2.5em 3.75em 7.875em;
  position: relative;
  min-height: 25em;
  &:nth-child(odd){
    background: #eef6ff;
  }
  &__icon{
    height: 3.75em;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 11.0625em;
  }
  &__content{
    @include grid100gap(1.25em);
    margin-top: 1.25em;
  }
}

.case{
  padding: 2.5em 0;
  &__grid{
    margin-top: 2.5em;
    row-gap: 2.5em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.5em;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.case-card{
  &__img{
    @include imgCover(70%);
    border-radius: .625em;
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.25em;
  }
  &__info{
    font-weight: 500;
    color: $gray;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 .625em;
    background: #f1f8ff;
    border-radius: 3.125em;
    height: 1.875em;
    margin-left: .875em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    color: $gray;
    font-weight: 500;
    svg{
      width: 1.5em;
      margin-right: .8125em;
    }
    &:hover{
      color: $primary;
      svg{
        transform: rotate(-45deg);
      }
      rect{
        stroke: $primary;
      }
    }
  }
}

.results{
  padding: 5em 0;
  margin: 2.5em auto;
  width: calc(100% - 2.5em);
  background-color: #061D61;
  //background-image: url(../img/results-bg.svg);
  //background-size: cover;
  //background-position: center;
  border-radius: 5em;
  position: relative;
  color: #fff;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    object-fit: cover;
  }
  &__top{
    text-align: center;
    @include grid100gap(1.25em);
  }
  &__desc{
    max-width: 36.7em;
    margin: 0 auto;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 3.25em / 3));
    align-items: flex-end;
    gap: 1.625em;
    margin-top: 3.75em;
  }
}

.result-card{
  min-height: 33.125em;
  border-radius: 2.5em 2.5em 0 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  text-align: center;
  padding: 9em 1em 5em;
  @media screen and (min-width: 577px){
    &:nth-child(2){
      min-height: 22.5em;
    }
    &:nth-child(3){
      min-height: 28.125em;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    backdrop-filter: blur(20px);
    background: linear-gradient(135deg, #057fe2 34.5%, #057fe2 89.5%);
  }
  &__icon{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #061d61;
    width: 12.5em;
    height: 12.5em;
    border-radius: 50%;
    @include center;
    img{
      width: 6.25em;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    @include grid100gap(1.25em);
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 5;
  }
  &__num2{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    font-weight: 900;
    width: 100%;
    display: flex;
    justify-content: center;
    overflow: hidden;
    span{
      font-size: 12.5em;
      line-height: 110%;
      -webkit-text-stroke: 2px #1c87c9;
      color: transparent;
      transform: translateY(.365em);
    }
  }
}


@media screen and (max-width: 992px){
  .service{
    padding: 2em 0;
    &__grid{
      margin-top: 1.25em;
    }
  }

  .service-card{
    font-size: .875em;
    padding: 1.25em 1.25em 6em;
    min-height: 18.75em;
    &__img{
      width: 8em;
    }
  }

  .case{
    padding: 1.25em 0;
    &__grid{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .case-card{
    font-size: .875em;
  }

  .results{
    font-size: .75em;
    border-radius: 2.5em;
  }

  .result-card{
    font-size: .75em;
  }
}


@media screen and (max-width: 576px){
  .service{
    &__grid{
      gap: .625em;
    }
  }

  .service-card{
    font-size: .75em;
  }

  .case{
    &__grid{
      grid-template-columns: 100%;
      row-gap: 1.5em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .results{
    font-size: 1em;
    padding: 2.5em 0;
    margin: 1.25em 0;
    width: 100%;
    border-radius: 1.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
      margin-top: 2em;
    }
  }

  .result-card{
    min-height: unset;
    font-size: 1em;
    border-radius: 2em;
    padding: 2em;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    &__icon{
      position: relative;
      z-index: 2;
      left: unset;
      font-size: .625em;
      top: unset;
      transform: unset;
      margin-bottom: 2em;
    }
  }
}
