.video-section{
  padding: 5em 0;
  overflow: hidden;
}

.video{
  width: calc(100% + 2.5em);
  margin-left: -1.25em;
  position: relative;
  display: block;
  &__img,
  video{
    width: 100%;
    border-radius: 9.375em;
    transform: rotate(3deg);
    display: block;
  }
  &__btn{
    @include centerAbsolute;
    @include center;
    width: 7.5em;
    height: 7.5em;
    border-radius: 50%;
    background-color: $primary;
    svg{
      width: 90%;
    }
  }
}

.marketing{
  background-color: #061D61;
  //background-image: url(../img/marketing-bg.svg);
  //background-size: cover;
  //background-position: center;
  border-radius: 5em;
  position: relative;
  width: calc(100% - 2.5em);
  margin: 5em auto;
  color: #fff;
  &__grid{
    padding: 5.25em 9em 5.25em 6.5em;
    display: grid;
    grid-template-columns: 38.75em 38.75em;
    justify-content: space-between;
    align-items: center;
    gap: 2em;
  }
  &__desc{
    max-width: 33.75em;
    margin-top: 2.5em;
    p{
      line-height: inherit;
      @include lastM(0 0 .75em);
    }
  }
  &__item {
    @include imgCover(100%);
  }
  @media screen and (min-width: 993px){
    &__item{
      &:nth-child(1){
        border-radius: 12.5em 0 0 0;
      }
      &:nth-child(3){
        border-radius: 0 12.5em 0 0;
      }
    }
    &__nav{
      padding-bottom: 100%;
      position: relative;
      grid-column: 1/2;
      grid-row: 2/3;
    }
    &__link{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      text-align: center;
      padding: 2.5em;
    }
  }
}

.main{
  padding-top: .625em;
  overflow: hidden;
  position: relative;
  &__top{
    margin: 0 auto;
    width: 56em;
    max-width: 100%;
    text-align: center;
    @include grid100gap(1.25em);
    padding-bottom: 12.5em;
    position: relative;
    z-index: 4;
  }
  &__desc{
    width: 43.75em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__nav{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: flex-start;
    bottom: -5.75em;
  }
  &__link{
    width: 15.625em;
    height: 15.625em;
    font-weight: 900;
    position: relative;
    z-index: 2;
  }
  &__link2{
    font-weight: 900;
    width: 12.5em;
    height: 12.5em;
    @include center;
    border: 1px solid $light-blue;
    border-radius: 50%;
    margin-left: -2em;
    &:hover{
      background-color: $light-blue;
      color: $black;
    }
  }
  .video{
    position: relative;
    z-index: 3;
  }
}

.companies{
  margin: -11em auto 0;
  width: calc(100% - 2.5em);
  padding: 17.25em 0 5.625em;
  background-color: #061D61;
  //background-image: url(../img/companies-bg.svg);
  //background-size: cover;
  //background-position: center;
  border-radius: 5em;
  color: #fff;
  overflow: hidden;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 14.25em;
    height: 14.25em;
    border-radius: 50%;
    background-color: #061d61;
    filter: blur(100px);
    right: -.625em;
    bottom: .9375em;
    z-index: 3;
    pointer-events: none;
  }
  //&:after{
  //  content: '';
  //  position: absolute;
  //  left: 0;
  //  bottom: 3.75em;
  //  width: 100%;
  //  height: 1px;
  //  background-color: #1b3582;
  //}
  &__grid{
    display: grid;
    grid-template-columns: 15em calc(100% - 16.5em);
    gap: 1.5em;
    align-items: center;
  }
}

.company-slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + .625em);
    }
    &-slide{
      width: 12.5%;
      padding-right: .625em;
    }
    &-nav{
      position: absolute;
      right: 0;
      top: 100%;
      margin-top: .5625em;
      display: flex;
    }
    &-button{
      margin: 0 0 0 .625em;
      width: 2.5em;
      height: 2.5em;
      border: 1px solid #1b3582;
      border-radius: 50%;
      position: static;
      svg{
        width: 1.125em;
      }
    }
  }
}

.company-item{
  position: relative;
  padding-bottom: 48.7%;
  img{
    max-width: 100%;
    max-height: 100%;
    @include centerAbsolute;
  }
}


@media screen and (max-width: 992px){
  .video-section{
    padding: 2.5em 0;
  }

  .video{
    width: 100%;
    margin-left: 0;
    &__img,
    video{
      border-radius: 0;
      transform: unset;
    }
    &__btn{
      font-size: .5em;
    }
  }

  .marketing{
    border-radius: 1.25em;
    &__grid{
      grid-template-columns: 100%;
      padding: 2em 0;
    }
    .h1{
      br{
        display: none;
      }
    }
    &__desc{
      margin-top: 1em;
      max-width: 100%;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    }
    &__nav{
      padding: 0;
      grid-column: 1/-1;
    }
  }

  .main{
    &__top{
      padding-bottom: 1.5em;
    }
    .h1{
      max-width: 80%;
      margin: 0 auto;
    }
    &__nav{
      position: static;
      transform: unset;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: .75em 1.25em;
    }
    &__link,
    &__link2{
      margin: 0;
      position: static;
      padding: 0 3em;
      height: 3.125em;
      width: 100%;
      flex-direction: row;
      border-radius: 2em;
      img{
        width: 2.5em;
        margin: 0 .5em 0 0;
      }
    }
  }

  .companies{
    width: 100%;
    margin-top: 0;
    border-radius: 0;
    padding: 2em 0;
    &:before,
    &:after{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__title{
      br{
        display: none;
      }
    }
  }

  .company-slider{
    .swiper{
      &-slide{
        width: 25%;
      }
      &-nav{
        position: static;
        justify-content: center;
      }
      &-button{
        margin: 0 .3125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .marketing{
    width: 100%;
    margin: 2em 0;
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
    &__link{
      text-align: left;
      padding: 0 1.5em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.875em;
      }
    }
  }

  .main{
    .h1{
      max-width: 100%;
    }
    &__nav{
      grid-template-columns: 100%;
    }
    &__link,
    &__link2{
      text-align: left;
      padding: 0 1.5em;
      span{
        font-size: 1em;
      }
    }
  }



  .company-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}
