.footer{
  padding: 3.125em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 6.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    display: flex;
    align-items: center;
    @include lastM(0 0 1.0625em);
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
}

.footer-address{
  display: flex;
  align-items: flex-start;
  &__item{
    @include lastM(0 0 1em);
  }
  img{
    width: 1.25em;
    margin-right: .625em;
  }
  b{
    font-weight: 500;
  }
}




@media screen and (max-width: 992px){
  .footer{
    &__inner{
      @include grid100gap(2em);
    }
  }
}


