.header{
  padding: 2.25em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    margin-right: 3.5em;
    width: 6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    &__item{
      width: 1.25em;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  flex-grow: 1;
  &__scroll{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__link{
    font-size: 1.125em;
    line-height: 140%;
    letter-spacing: -0.01em;
    font-weight: 500;
  }
  &__btn{
    padding: 0 2em;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1.125em;
    line-height: 140%;
    letter-spacing: -0.01em;
    @include lastM(0 2.22em 0 0);
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      z-index: 11;
      position: relative;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 5em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
    &__btn{
      margin: 2em 0 0;
      width: 100%;
      height: 3em;
    }
    .socials{
      margin: 2em 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1em);
    }
  }
}


